<template>
  <div class="container">
    <Sidebar active="settings" />
    <main>
      <h1>SETTINGS</h1>
      <section>
        <form @submit.prevent="save">
          <div class="form-group">
            <label for="route">BASE-URL</label>
            <input
              type="text"
              class="inp"
              id="route"
              readonly
              v-model="url"
            />
          </div>
          <div class="form-group">
            <label for="userid">USER-ID</label>
            <input
              type="text"
              class="inp"
              id="userid"
              readonly
              :value="token"
            />
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";
export default {
  name: "Settings",
  components: {
    Sidebar,
  },
  computed: {
    token() {
      return this.$store.getters.token;
    },
    url() {
      return 'http://' + location.host + '/api/' + '<user-id>' + '/<route>';
    },
  },
};
</script>

